<template>
  <v-app>
    <v-main class="py-16">
      <v-container class="mt-16" v-if="!pageLoaded">
        <v-skeleton-loader
          v-bind="attrs"
          class="mb-10"
          type="card-heading, article, card-heading, article"
        ></v-skeleton-loader>
      </v-container>
      <v-container
        :class="[{ dark_one: darkMode }, { loaded_style: !pageLoaded }]"
        class="mt-15 rounded-lg pt-5"
      >
        <v-btn color="error" @click="userSignOut">
          <v-icon left>mdi-login-variant</v-icon>
          <span>تسجيل الدخول بحساب اخر</span>
        </v-btn>
        <v-alert type="info" class="my-16"> الرسائل </v-alert>

        <template v-if="false">
          <v-simple-table fixed-header height="400px" dense>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="">الاسم</th>
                  <th class="text-">الايميل</th>
                  <th class="text-">الموضوع</th>
                  <th class="text-">الرسالة</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, i) in newMessages" :key="i">
                  <td>{{ item.username }}</td>
                  <td>{{ item.email }}</td>
                  <td>{{ item.subject }}</td>
                  <td>{{ item.message }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </template>
        <div class="messages">
          <div class="message_group" v-for="(item, i) in newMessages" :key="i">
            <div class="parent pa-3">
              <h3 class="d-flex flex-column">
                <span>الاسم: {{ item.username }}</span>
                <span
                  >الايميل:
                  <span class="primary--text">{{ item.email }}</span></span
                >
                <span>الموضوع: {{ item.subject }}</span>
              </h3>
            </div>
            <div class="child px-3 pa-2 mt-3" style="word-break: break-word">
              الرسالة: {{ item.message }}
            </div>
            <v-divider class="my-10"></v-divider>
          </div>
        </div>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { getDatabase, ref, child, get } from "firebase/database";
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      messages: {},
      pageLoaded: false,
      newMessages: [],
      attrs: {
        class: "mb-10",
        boilerplate: false,
        elevation: 2,
      },
      desserts: [
        {
          name: "Frozen Yogurt",
          calories: 159,
          subject: "fdasfdsaf",
          message: "fdsafsssssssssssssssssssssssssssssssssssssssssssss",
        },
        {
          name: "Ice cream sandwich",
          calories: 237,
        },
        {
          name: "Eclair",
          calories: 262,
        },
        {
          name: "Cupcake",
          calories: 305,
        },
        {
          name: "Gingerbread",
          calories: 356,
        },
        {
          name: "Jelly bean",
          calories: 375,
        },
        {
          name: "Lollipop",
          calories: 392,
        },
        {
          name: "Honeycomb",
          calories: 408,
        },
        {
          name: "Donut",
          calories: 452,
        },
        {
          name: "KitKat",
          calories: 518,
        },
      ],
    };
  },
  mounted() {
    this.checkIfSigned();
    this.getMessagesFromFireBase();
    this.isPageLoaded();
  },
  computed: {
    ...mapState({
      isUserIn: (store) => store.port.isUserIn,
      darkMode: (store) => store.port.S_darkMode,
    }),
  },
  methods: {
    isPageLoaded() {
      // document.onreadystatechange = () => {
      // if (document.readyState == "complete") {
      setTimeout(() => {
        // console.log("page loaded");
        this.pageLoaded = true;
      }, 1500);
      // }
      // };
    },
    ...mapActions(["A_isUserIn"]),
    getMessagesFromFireBase() {
      const dbRef = ref(getDatabase());
      get(child(dbRef, `messages`))
        .then((snapshot) => {
          if (snapshot.exists()) {
            // console.log(snapshot.val());
            this.messages = snapshot.val();
            Object.keys(this.messages).forEach((el) => {
              this.newMessages.push(this.messages[el]);
            });
          } else {
            console.log("No data available");
          }
        })
        .catch((error) => {
          console.error(error);
        });
    },
    checkIfSigned() {
      const auth = getAuth();
      onAuthStateChanged(auth, (user) => {
        if (user) {
          // User is signed in, see docs for a list of available properties
          this.A_isUserIn(true);
          // https://firebase.google.com/docs/reference/js/firebase.User
          // eslint-disable-next-line no-unused-vars
          const uid = user.uid;
          // console.log("signed?", this.isUserIn);
          // console.log("user is signed in", uid);f
          // ...
        } else {
          this.A_isUserIn(false);
          this.$router.push({ path: "/admin/auth" });
          // console.log("user is signed out");
          // User is signed out
          // ...
        }
      });
    },
    userSignOut() {
      const auth = getAuth();
      signOut(auth)
        .then(() => {
          // Sign-out successful.
          // console.log("user signed out");
        })
        .catch((error) => {
          // An error happened.
          console.log(error);
        });
      this.checkIfSigned();
    },
  },
};
</script>

